import React from 'react';
import { HashRouter, Link } from 'react-router-dom';

const Logo = () => {
	return (
		<HashRouter>
			<div className="logo-wrapper">
				<Link reloadDocument className='logo' to="/borrow-market">DeDeLend</Link>
			</div>
		</HashRouter>
		
	);
};

export default Logo;